@import url('https://fonts.googleapis.com/css?family=Raleway');

html {
  height: 100%;
}

body {
  background-image: -webkit-linear-gradient( 350deg, #F97794 0%, #623AA2 100%);
  background-image: -o-linear-gradient( 350deg, #F97794 0%, #623AA2 100%);
  background-image: linear-gradient( 100deg, #F97794 0%, #623AA2 100%);
  background-size: cover;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}



